import React, { Component } from "react";
import styled from "styled-components/native";
import Text from './Text';

const StyledButton = styled.TouchableOpacity.attrs({
  backgroundColor: props => ((props.header || props.bordered )? 'transparent' : props.danger ? 'red' : props.success ? "green" : "blue"),
  borderWidth: props => (props.bordered ? 1 : 0),
  borderColor: props => (props.danger ? 'red' : props.success ? "green" : "blue")
})`
  backgroundColor: ${props => props.backgroundColor};
  paddingVertical: 8;
  paddingHorizontal: 15;
  borderRadius: 6;
  borderColor: ${props => props.borderColor};
  borderWidth: ${props => props.borderWidth};
  height: 45;
  flexDirection: row;
  alignItems: center;
  justifyContent: space-between;
`;


export { StyledButton as Button };
